:root {
  --font-family: "Inter", sans-serif;
  --second-family: "Noto Serif", sans-serif;
  --container-offset: 10px;
  --content-width: 1024px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --content-inner-width: 722px;
  --container-inner-width: calc(var(--content-inner-width) + (var(--container-offset) * 2));
  --title-main-size: 40px;
  --title-size: 26px;
  --main-size: 22px;
  --second-size: 18px;
  --default-size: 16px;
  --small-size: 14px;
  --light-color: #fff;
  --basic-color: #16293f;
  --accent-color: #0e0865;
  --blue-color: #293389;
  --error-color: red;
  --success-color: green;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable value-keyword-case */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-inner {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-inner-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
}

.advice {
  padding-top: 350px;
  color: var(--basic-color);
}
.advice__title {
  font-weight: 700;
  font-size: var(--title-main-size);
}
.advice__quote {
  display: block;
  margin-top: 32px;
  max-width: 342px;
  font-weight: 500;
  font-size: var(--main-size);
}
.advice__text {
  margin-top: 77px;
  font-weight: 500;
  font-size: var(--main-size);
  line-height: 1.25;
}
.advice__user {
  margin-top: 32px;
  margin-right: 182px;
  margin-left: auto;
  max-width: 320px;
}

.illustration {
  margin-top: 30px;
}

.author {
  margin-top: 10px;
  border-bottom: 1px solid rgba(22, 41, 63, 0.8);
  padding-bottom: 10px;
  font-weight: 500;
  font-size: var(--default-size);
  opacity: 0.6;
}

.user {
  column-gap: 8px;
  align-items: center;
}
.user__block-wrap {
  align-items: center;
}
.user__block-wrap span {
  margin-left: 40px;
  font-weight: 500;
  font-size: 14px;
  opacity: 0.4;
}
.user__name {
  font-weight: 700;
  font-size: var(--default-size);
}
.user__comment {
  margin-top: 15px !important;
}
.user__descr {
  margin-top: 4px;
  font-weight: 500;
  font-size: var(--default-size);
}
.user__img {
  display: block;
  margin-top: 15px;
  max-width: 131px;
}

.article {
  font-family: var(--second-family);
  color: var(--basic-color);
}
.article .nav {
  margin-top: 38px;
  margin-left: -10px;
}
.article .nav__item + .nav__item {
  margin-top: 17px;
}
.article .nav__link {
  position: relative;
  border-bottom: 1px solid var(--blue-color);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 20px;
  letter-spacing: normal;
  color: var(--blue-color);
  transition: ease-in-out 0.3s;
}
.article .nav__link::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: var(--blue-color);
}
.article .nav__link:hover {
  opacity: 0.7;
}
.article__title {
  padding-top: 52px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: var(--title-size);
  color: var(--accent-color);
}
.article__title:nth-of-type(2) {
  margin-left: 5px;
}
.article__note {
  position: relative;
  margin-top: 28px;
  padding-left: 25px;
  font-weight: 500;
  font-size: var(--second-size);
  line-height: 1.4;
}
.article__note::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 4px;
  width: 2px;
  height: 95%;
  background: var(--blue-color);
}
.article__note span {
  position: relative;
  color: var(--blue-color);
  transition: ease-in-out 0.3s;
}
.article__note span:hover {
  opacity: 0.7;
}
.article__text + .article__note {
  margin-top: 20px;
}
.article__text {
  margin-top: 25px;
  font-size: var(--second-size);
  line-height: 1.4;
}
.article__example {
  margin-top: 28px;
  margin-left: 7px;
  font-weight: 700;
  font-style: italic;
  font-size: var(--second-size);
  line-height: 1.4;
}
.article .user {
  margin: 0 auto;
  max-width: none;
}
.article__blockquote {
  margin-top: 37px;
  border-radius: 10px;
  padding: 30px 0 35px;
  font-family: var(--font-family);
  background: #d8eafc;
}
.article__quote {
  margin-top: 30px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: var(--main-size);
  line-height: 1.25;
}
.article .illustration {
  margin-top: 50px;
  font-family: var(--font-family);
}
.article .blockquote-addiction {
  margin-top: 150px;
  border: 1px solid #2bd5df;
  border-radius: 10px;
  padding: 56px 0;
  background: transparent;
}
.article .quote-addiction {
  font-family: var(--second-family);
  line-height: 1.4;
}
.article .user-addiction {
  margin-top: 22px;
}

.article-steps__subtitle {
  margin-top: 55px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 20px;
  color: var(--blue-color);
}
.article-steps__list {
  margin-top: 33px;
}
.article-steps__item + .article-steps__item {
  margin-top: 16px;
}
.article-steps__note {
  margin-top: 16px;
}
.article-steps__note span {
  font-weight: 700;
}
.article-steps__item, .article-steps__note {
  font-weight: 500;
  font-size: var(--second-size);
  line-height: 1.4;
}

.reply {
  padding: 95px 0 88px;
}
.reply__wrapper {
  padding: 0 35px;
}
.reply__title {
  font-weight: 700;
  font-size: 32px;
  color: var(--accent-color);
}
.reply__sum {
  display: block;
  margin-top: 37px;
  font-weight: 500;
  font-size: 20px;
  color: var(--basic-color);
  opacity: 0.8;
}
.reply__form {
  margin-top: 30px;
  border: 1px solid rgba(22, 41, 63, 0.4);
  border-radius: 10px;
  padding: 16px 32px 16px 12px;
}
.reply__user {
  column-gap: 12px;
  align-items: center;
}
.reply__user--post {
  align-items: start;
}
.reply__btns {
  display: flex;
  column-gap: 15px;
  align-items: center;
  justify-content: end;
  margin-top: 10px;
}
.reply__comments {
  margin-top: 40px;
}
.reply__answer {
  margin-top: 18px;
  margin-left: 50px;
}
.reply__answer-link {
  font-weight: 500;
  font-size: 16px;
  color: var(--basic-color);
  opacity: 0.8;
}
.reply__answer-num {
  position: relative;
  margin-left: 70px;
}
.reply__answer-num::before, .reply__answer-num::after {
  content: url("img/arrow-comment.png");
  position: absolute;
  top: 50%;
  width: 27px;
  height: 27px;
  transform: translateY(-50%);
}
.reply__answer-num::before {
  left: -39px;
}
.reply__answer-num::after {
  right: -39px;
  transform: translateY(-50%) rotate(180deg);
}
.reply__comment {
  margin-top: 36px;
  margin-left: 52px;
}
.reply__comment-num {
  margin-left: 55px;
}
.reply__comment-num::before, .reply__comment-num::after {
  width: 24px;
  height: 24px;
}
.reply__comment-num::before {
  left: -32px;
}
.reply__comment-num::after {
  right: -32px;
  transform: translateY(-35%) rotate(180deg);
}

.text-field__message {
  margin-top: 10px;
  border: transparent;
  padding: 5px 0;
  width: 100% !important;
  height: 25px;
  font-size: 14px;
  line-height: 14px;
  color: var(--basic-color);
  resize: unset;
}

textarea.text-field__message::placeholder {
  font-size: 14px;
  line-height: 14px;
  color: var(--basic-color);
  opacity: 0.8;
}

.text-field__main--error,
.text-field__img--error,
.text-field__answer--error,
.text-field__submit--success {
  display: none;
  margin-top: 5px;
  font-size: 12px;
  color: var(--error-color);
  opacity: 0.65;
}

.text-field__submit--success {
  color: var(--success-color);
}

.button {
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: var(--second-size);
  color: var(--light-color);
  background: #8185c8;
  transition: ease-in-out 0.3s;
  cursor: pointer;
}

.button:hover {
  opacity: 0.7;
}

.accordeon {
  margin-top: 65px;
}
.accordeon-wrapper {
  border-radius: 10px;
  padding: 32px 0;
  background: #d8eafc;
}
.accordeon__content {
  overflow: hidden;
  max-height: 0;
  font-size: var(--default-size);
  opacity: 0;
  transition: all 0.3s;
}
.accordeon__box:first-child {
  margin-top: 0;
}
.accordeon__btn {
  position: relative;
  outline: none;
  border: none;
  padding: 0 50px 0 0;
  width: 100%;
  font-weight: 500;
  font-size: var(--main-size);
  text-align: left;
  color: var(--basic-color);
  background: transparent;
  transition: ease-in-out 0.3s;
  cursor: pointer;
}
.accordeon__btn::after {
  content: url("img/arrow-accordeon.png");
  position: absolute;
  right: 0;
  top: 50%;
  width: 45px;
  height: 45px;
  transform: translateY(-50%) rotate(0);
  transition: ease-in-out 0.3s;
}
.accordeon .active.accordeon__btn::after {
  transform: translateY(-50%) rotate(-180deg);
  transition: ease-in-out 0.3s;
}
.accordeon .active.accordeon__btn + .accordeon__content {
  margin-top: 30px;
  opacity: 1;
  transition: ease-in-out 0.3s;
}

.upload {
  position: relative;
  border-radius: 5px;
  background: #ccc;
}

.btn {
  border-radius: 5px;
  width: 27px;
  height: 27px;
  background: #fff;
  background-image: url(img/upload-icon.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: ease-in-out 0.3s;
  cursor: pointer;
}

.btn.primary {
  position: absolute;
  right: 0;
  top: 10px;
  margin-top: -10px;
  border: none;
  border-radius: 5px;
  width: 100px;
  font-size: var(--second-size);
  color: #fff;
  background: #8185c8;
}

.btn:hover {
  background-color: #ccc;
  cursor: pointer;
}

input[type=file] {
  display: none;
}

.preview-image {
  position: relative;
  overflow: hidden;
  margin: 5px;
}

.preview-image.removing {
  transform: scale(0);
  transition: transform 0.3s;
}

.preview-image img {
  width: 180px;
  height: auto;
}

.preview-image:hover .preview-remove {
  opacity: 1;
}

.preview-image:hover .preview-info {
  bottom: 4px;
}

.preview-remove {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  font-weight: 700;
  background: rgba(255, 255, 255, 0.5);
  opacity: 0;
  transition: opacity 0.22s;
  cursor: pointer;
}

.preview-info {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2px;
  min-height: 24px;
  font-size: 0.8rem;
  background: rgba(255, 255, 255, 0.5);
  transition: bottom 0.22s;
}

@media (max-width: 1070px) {
  .advice {
    padding-top: 35px;
  }
  .advice__title {
    font-size: var(--title-size);
  }
  .advice__quote {
    margin-top: 20px;
    font-size: var(--default-size);
  }
  .advice__user {
    margin: 20px 0 0;
  }
  .user__name,
  .user__descr,
  .user__comment,
  .user__block-wrap span,
  .reply__answer-link,
  .reply__answer-num,
  .author {
    font-size: var(--small-size);
  }
  .illustration {
    margin-top: 20px;
  }
  .advice__text {
    margin-top: 20px;
    font-size: var(--default-size);
  }
  .article__title {
    padding-top: 25px;
    font-size: var(--main-size);
  }
  .article .nav {
    margin-top: 15px;
  }
  .nav__link {
    font-size: var(--default-size) !important;
  }
  .article__note {
    margin-top: 15px;
    padding-left: 10px;
    font-size: var(--small-size);
  }
  .article__text,
  .article__example {
    margin-top: 15px;
    margin-left: 0;
    font-size: var(--default-size);
  }
  .article__blockquote {
    margin-top: 20px;
    padding: 15px;
  }
  .article__quote {
    margin-top: 10px;
    font-size: var(--default-size);
  }
  .article .illustration {
    margin-top: 20px;
  }
  .article .blockquote-addiction {
    margin-top: 20px;
    padding: 15px;
  }
  .article-steps__subtitle {
    margin-top: 20px;
    font-size: var(--main-size);
  }
  .article-steps__list {
    margin-top: 10px;
  }
  .article-steps__item,
  .article-steps__note {
    font-size: var(--default-size);
  }
  .article-steps__item + .article-steps__item {
    margin-top: 5px;
  }
  .accordeon {
    margin-top: 20px;
  }
  .accordeon-wrapper {
    padding: 15px;
  }
  .accordeon__btn {
    font-size: var(--second-size);
  }
  .accordeon .active.accordeon__btn + .accordeon__content {
    margin-top: 20px;
  }
  .accordeon__content {
    font-size: var(--small-size);
  }
  .reply {
    padding: 30px 0;
  }
  .reply__title {
    font-size: 24px;
  }
  .reply__sum {
    margin-top: 20px;
    font-size: var(--small-size);
  }
  .reply__form {
    margin-top: 15px;
    padding: 15px 10px;
  }
  .button,
  .btn.primary {
    font-size: var(--small-size);
  }
  .reply__comments,
  .reply__comment {
    margin-top: 20px;
  }
}
@media (max-width: 768px) {
  .reply__wrapper {
    padding: 0;
  }
  .user__block-wrap span {
    margin-left: 20px;
  }
}